class Word {
    constructor (tiles) {
        this.collection = tiles;
    }

    isFulfilled () {
        const fulfilled = 0;
        this.collection.forEach((tile) => {
            if (tile.isFulfilled()) fulfilled++;
        });
        return fulfilled === this.collection.length;
    }
}

module.exports = class Words {
    constructor () {
        this.collection = {};
    }

    set (word, LetterTiles) {
        this.collection[word] = new Word(LetterTiles);
        return LetterTiles;
    }

    get (word) {
        return this.collection[word];
    }

    isFulfilled () {
        const fulfilled = 0;
        this.collection.forEach((word) => {
            if (word.isFulfilled()) fulfilled++;
        });
        return fulfilled === this.collection.length;
    }
}


