const Words = require('./Words');
const Tile = require('./Tile');

module.exports = class Grid {

    constructor (_x, _y) {
        this.words = new Words();
        this.collection = [];
        for (let x = 0; x < _x; x++) {
            for (let y = 0; y < _y; y++) {
                this.collection.push( new Tile(x,y) );
            }
        }
    }

    get (x, y) {
        return this.collection.find( (tile) => tile.x === x && tile.y === y);
    }

    setLetter (x, y, value) {
        if (String(value).length !== 1) throw new Error('`value` must be a String of length 1');
        const tile = this.get(x, y);
        Object.assign(tile, {
            value,
            isInUse: true
        });
        return tile;
    }

    setInput (x, y, value) {
        const tile = this.setLetter(x, y, value);
        tile.isEditable = true;
        return tile;
    }

    setWord (word, coordSets) {
        const letterTiles = coordSets.map( (coords, index) => {
            const x = coords[0];
            const y = coords[1];
            const letterTile = this.setInput(x, y, word[index]);
            return letterTile
        });

        this.words.set(word, letterTiles);

        return letterTiles;
    }

}


