module.exports = class Tile {

    constructor (x, y) {
        Object.assign(this, {
            x, y,
            isInUse: false,
            isEditable: false,
            value: "",
            input: ""
        });
    }

    isFulfilled () {
        return this.value.toLowerCase() === this.input.toLowerCase();
    }

}


